* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  outline: none;
  scroll-behavior: smooth;
  font-family: 'Poppins' , sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(185, 221, 243);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading{
  text-align: center;
  padding: 2rem;
  padding-bottom: 0;
  text-transform: capitalize;
}

.booklist{
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  padding: 2rem;
}

.book{
  position: relative;
  background-color: #fff;
  border-radius: 12px;
  padding: 1.5rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 300px;
}

.book img{
  width: 180px;
  height: 260px;
  padding: 5px;
}

.book h4 , .book h5{
  padding: 5px;
  margin: 2px;
  font-weight: 700;
  text-align: center;
}

.book button{
  font-size: 1rem;
  padding: 5px;
  margin: 2px;
  border-radius: 4px;
  cursor: pointer;
}

.book .index{
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  font-size: 1rem;
  background-color:#c35600;
  color: #fff;
  border-top-left-radius: 12px;
  border-bottom-right-radius: 12px;
}








